import type { EventName } from "@lit-labs/react";
import * as WebComponentModule from "@vaadin/details/vaadin-details.js";
import * as React from "react";
import { createComponent, WebComponentProps } from "../utils/createComponent.js";
export type DetailsEventMap = Readonly<{
    onOpenedChanged: EventName<WebComponentModule.DetailsEventMap["opened-changed"]>;
}>;
const events = { onOpenedChanged: "opened-changed" } as DetailsEventMap;
export type DetailsProps = WebComponentProps<WebComponentModule.Details, DetailsEventMap>;
export const Details = createComponent({
    elementClass: WebComponentModule.Details,
    events,
    react: React,
    tagName: "vaadin-details"
});
export { WebComponentModule };
